import React from "react";

export const MarkContext = React.createContext(null);

export const MarksProvider = ({ children }) => {
  const [title, setTitle] = React.useState("Add Marks");
  const [openBox, setOpenBox] = React.useState(false);
  //   const [backToOption, setBackToOption] = React.useState(false);
  const [openAssign, setOpenAssign] = React.useState(false);
  const [openQuizz, setOpenQuizz] = React.useState(false);
  const [openLab, setOpenLab] = React.useState(false);
  const [openAttendance, setOpenAttendance] = React.useState(false);
  const [label, setLabel] = React.useState(null);
  const [classInfo, setClassInfo] = React.useState(null);
  const [reload, setReload] = React.useState(false);
  const [isBack, setIsBack] = React.useState(false);
  const [search, setSearch] = React.useState("");
  const [sortTable, setSortTable] = React.useState({ order: "", field: "" });

  const handleOpenBox = (flag) => {
    if (!flag) {
      setReload(true);
    }
    setOpenBox(flag);
    setOpenAssign(false);
    setOpenAttendance(false);
    setOpenLab(false);
    setOpenQuizz(false);
    setLabel(null);
    setTimeout(() => {
      setReload(false);
    }, 10);
  };

  const handleBackToOption = (flag) => {
    setIsBack(flag);
    if (!flag) {
      setOpenAssign(false);
      setOpenAttendance(false);
      setOpenLab(false);
      setOpenQuizz(false);
      setLabel(null);
    }
  };
  const handleOpenAssignment = (flag) => {
    setOpenAssign(flag);
  };

  const handleOpenQuizz = (flag) => {
    setOpenQuizz(flag);
  };

  const handleOpenAttendance = (flag) => {
    setOpenAttendance(flag);
  };
  const handleOpenLab = (flag) => {
    setOpenLab(flag);
  };

  const handleSetLabel = (str) => {
    setLabel(str);
  };

  const handleSetTitle = (str) => {
    setTitle(str);
  };

  const handleSetClassInfo = (data) => {
    setClassInfo(data);
  };

  const handleSearch = (e) => {
    setSearch(e.target.value);
  };

  const handleSort = (obj) => {
    setSortTable(obj);
  };

  const handleReload = (flag) => {
    setReload(flag);
  };

  return (
    <MarkContext.Provider
      value={{
        handleOpenBox,
        handleBackToOption,
        handleOpenAssignment,
        handleOpenQuizz,
        handleSetLabel,
        handleOpenAttendance,
        handleOpenLab,
        handleSetTitle,
        handleSetClassInfo,
        handleSearch,
        handleSort,
        handleReload,
        openBox,
        openAssign,
        openAttendance,
        openLab,
        openQuizz,
        label,
        title,
        classInfo,
        isBack,
        search,
        sortTable,
        reload,
        // backToOption,
      }}
    >
      {children}
    </MarkContext.Provider>
  );
};

export const useMarksEntry = () => {
  return React.useContext(MarkContext);
};
