import React, { useState } from "react";
import { toast } from "react-toastify";
import readXlsxFile from "read-excel-file";
import useAxios from "../api/Axios";
import url from "../components/download/Add All Students.xlsx";
import { useAuth } from "./Auth";

const AddStudentContext = React.createContext(null);

export const AddStudentProvider = ({ children }) => {
  const axios = useAxios();
  const auth = useAuth();
  const [openBox, setOpenBox] = useState(false);
  const [classInfo, setClassInfo] = useState(null);
  const [reload, setReload] = useState(false);

  const [rows, setRows] = React.useState([]);
  const [xlFile, setXlFile] = React.useState(null);
  const [isImport, setIsImport] = React.useState(false);
  const [array, setArray] = React.useState([]);

  const [isIndividual, setIsIndivisual] = React.useState(false);

  const [isImportOther, setIsImportOther] = React.useState(false);
  const [otherInfo, setOtherInfo] = useState({
    center: auth.user.center,
  });
  const [isViewOther, setIsViewOther] = useState(false);

  const [loading, setLoading] = useState(true);
  const [isBack, setBack] = useState(false);

  const handleOpen = (flag) => {
    if (!flag) {
      setReload(true);
    } else {
      setIsImport(false);
      setIsViewOther(false);
      setIsIndivisual(false);
      setIsImportOther(false);
      setOtherInfo({ center: auth.user.center });
      setBack(true);
      setOpenBox(true);
      setReload(false);
      setLoading(false);
      setRows([]);
      setXlFile("");
      setArray([]);
    }
    setOpenBox(flag);
    setBack(false);
  };

  const handleSetClassInfo = (data) => {
    setClassInfo(data);
  };

  const setXlsxFile = (file) => {
    const name = file.name;
    // const extention = name.substring(name.indexOf("."), name.length);
    const flag = name.toString().includes(".xlsx");
    if (flag) {
      setXlFile(file);
      readXlsxFile(file)
        .then((row) => {
          handleSetRow(row);
        })
        .catch((err) => {
          handleError(err);
        });
    } else {
      handleError(`' ${name} ' not support. Please import XLSX file instead`);
      document.getElementsByName("oFile")[0].value = "";
    }
  };

  const handleImportSubmit = async () => {
    try {
      const url = "/student/import";
      await axios
        .post(url, { data: array })
        .then((res) => {
          handleSuccess(res.data);
          setArray([]);
          setRows([]);
          setXlFile("");
          handleOpen(false);
          handleIsImport(false);
        })
        .catch((err) => {
          console.log(err);
          handleError(err?.response?.data?.message);
        });
    } catch (error) {
      handleError(error?.response?.data?.message);
    }
  };

  const handleError = (msg) => {
    toast.error(msg, {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "light",
    });
  };

  const handleSuccess = (msg) => {
    toast.success(msg, {
      position: "top-right",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "light",
    });
  };

  const handleSetRow = (row) => {
    setRows(row);
    let dataArray = [];
    const Arr = row;
    for (let i = 1; i < Arr.length; i++) {
      let objArr = [
        Arr[i][0],
        `${Arr[i][1]}`,
        Number(classInfo.classId),
        classInfo.year,
        classInfo.semester,
        `${classInfo.courseName}`,
        classInfo.term,
        new Date().toUTCString(),
      ];
      dataArray.push(objArr);
    }
    setArray(dataArray);
  };

  const handleSetIndividual = (flag) => {
    setIsIndivisual(flag);
    setBack(flag);
  };

  const handleIsImport = (flag) => {
    setIsImport(flag);
    setBack(flag);
  };
  const handleIsImportOther = (flag) => {
    setIsImportOther(flag);
    setBack(flag);
  };

  const handleDownload = () => {
    const link = document.createElement("a");
    link.href = url;
    link.setAttribute(
      "download",
      `Import_Student_Name_Sample_File_${Date.now()}.xlsx`
    ); //set the attribute of the <a> link tag to be downloadable when clicked and name the sheet based on the date and time right now.

    document.body.appendChild(link);
    link.click(); //programmatically click the link so the user doesn't have to

    document.body.removeChild(link);
    URL.revokeObjectURL(url); //important for optimization and preventing memory leak even though link element has already been removed.
  };

  const handleOtherImport = (flag) => {
    setIsImportOther(flag);
    setIsViewOther(!flag);
    setBack(true);
  };

  const handleResetRowsAndArray = () => {
    setRows([]);
    setArray([]);
  };

  /**
   * For Individual Student Entry
   */
  const handleSubmitIndividual = async (data, handleReset) => {
    try {
      const url = "/import/students";
      await axios
        .post(url, { data })
        .then((res) => {
          handleReset();
          handleSuccess(res.data);
          setBack(false);
        })
        .catch((err) => {
          handleError(err?.response?.data?.message);
        });
    } catch (error) {
      handleError(error?.response?.data?.message);
    }
  };

  const handleIncrementArray = (idx, data, setData) => {
    setLoading(true);
    const newArray = [];
    data.map((d, index) => {
      newArray.push(d);
      if (index === idx) {
        newArray.push([
          "",
          "",
          Number(classInfo.classId),
          classInfo.year,
          classInfo.semester,
          classInfo.courseName,
          classInfo.term,
          new Date().toUTCString(),
        ]);
      }
      return null;
    });
    setData(newArray);
    setTimeout(() => {
      setLoading(false);
    }, 100);
  };

  const handleDecrementArray = (idx, data, setData) => {
    setLoading(true);
    data.splice(idx, 1);
    const newData = data;
    setData(newData);
    setTimeout(() => {
      setLoading(false);
    }, 100);
  };

  const handleOtherFormImportChange = (e) => {
    setOtherInfo({ ...otherInfo, [e.target.name]: e.target.value });
  };

  const handleBack = (flag) => {
    setBack(flag);
    if (!flag) {
      setIsImport(false);
      setIsIndivisual(false);
      setIsImportOther(false);
      setIsViewOther(false);
    }
  };

  const handleSubmitImportfromOther = async (arr) => {
    try {
      const url = "/student/import";
      await axios
        .post(url, { data: arr })
        .then((res) => {
          handleSuccess(res.data);
          setArray([]);
          setRows([]);
          setXlFile("");
          handleOpen(false);
          handleIsImport(false);
          setIsImportOther(false);
          setIsViewOther(false);
        })
        .catch((err) => {
          handleError(err?.response?.data?.message);
        });
    } catch (error) {
      handleError(error?.response?.data?.message);
    }
  };

  const handleReload = (flag) => {
    setReload(flag);
  };

  return (
    <AddStudentContext.Provider
      value={{
        classInfo,
        openBox,
        otherInfo,
        rows,
        xlFile,
        isImport,
        isImportOther,
        isIndividual,
        array,
        loading,
        isViewOther,
        isBack,
        reload,
        handleSubmitImportfromOther,
        handleOtherFormImportChange,
        handleOpen,
        handleSetClassInfo,
        setXlsxFile,
        handleImportSubmit,
        handleError,
        handleSuccess,
        handleSetIndividual,
        handleIsImport,
        handleIsImportOther,
        handleDownload,
        handleResetRowsAndArray,
        handleSubmitIndividual,
        handleDecrementArray,
        handleIncrementArray,
        handleOtherImport,
        handleBack,
        setLoading,
        setArray,
        handleReload,
      }}
    >
      {children}
    </AddStudentContext.Provider>
  );
};

export const useAddStudent = () => {
  return React.useContext(AddStudentContext);
};
